import React from 'react'
import { Link } from "react-router-dom";

function Welcome() {
  return (
    <div className="welcome">
      <div className="welcome-container">
        <h2>WELCOME</h2>
        <p>please log in.</p>
        <p>signup for free if you dont have an account </p>
        <div className="enter"> 
          <Link to="/login">
          <button>LogIn</button>
          </Link> 
          <Link to="/signup">
          <button>Sign Up</button>
          </Link>
        </div>
      </div>
    </div>
  )
}

export default Welcome
