import React, { useState } from 'react'
import { Link } from "react-router-dom";

function Signup() {

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  
  const signupDetails = {
    name,
    email,
    password
  }
  const handleSignup=()=>{
    console.log(name, email)
    localStorage.setItem("signup", JSON.stringify(signupDetails))
  }

  const handleInput =(e)=>{
    if(e.target.name === "name"){
      setName(e.target.value)
    }else if(e.target.name === "email"){
      setEmail(e.target.value)
    }else if(e.target.name === "password"){
      setPassword(e.target.value)
    }
    
  }


  return (
    <div className="signup">
      <div className="signup-container">
        <h2>Sign up</h2>
        <input type="text" placeholder="Enter name" name="name" value={name} onChange={handleInput}/>
        <input type="email" placeholder="Enter email" value={email} name="email" onChange={ handleInput}/>
        <input type="password" placeholder="Enter password" value={password}  name="password" onChange={handleInput}/>
        <div className="checklog">
            <input type="checkbox" className="input" name=""/>
            <span className="span">i accept terms and condition</span>
        </div>
        <Link to="/login">
        <input type="submit" value="Sign up" onClick={handleSignup}/>
        </Link>
      </div>
    </div>
  )
}

export default Signup
