import React, { useState } from 'react';
import { Link } from "react-router-dom";

function Login() {

  const [loginEmail, setLoginEmail] = useState("");
  const [loginPassword, setLoginPassword] = useState("");

  const [ authentication, setAuthentication ] = useState(false)
  const [ error, setError ] = useState("")
  

  const handleChange = (e)=>{
    if(e.target.name === "loginEmail"){
      setLoginEmail(e.target.value)
    }else if(e.target.name === "loginPassword"){
      setLoginPassword(e.target.value)
    }
  }
  const handleLogin = (e)=>{
    e.preventDefault();

    let result = JSON.parse(localStorage.getItem("signup"))

    if(result.email === loginEmail && result.password === loginPassword){
      setAuthentication(true);
      console.log("authenticated");
    }else{
      setError("wrong username or password")
      console.log("error auth");
    }
    console.log(result.email);
  }
  let auth = authentication?"profile":"login";

  return (
    <div className="login">
      <div className="login-container">
        <h2>Log in!</h2>
        {error}
        <form>
          <input 
          type="text" 
          value={loginEmail}
          name="loginEmail"
          onChange={handleChange} 
          placeholder="Enter email" 
          />
          <input 
          type="text" 
          value={loginPassword}
          name="loginPassword"
          onChange={handleChange}
          placeholder="Enter password"
          />
          <div className="checklog">
            <div className="check">
              <input type="checkbox" name=""/>
              <span>Remember</span>
            </div>
            <Link to="/signup">
            <span>forgotten password?</span>
            </Link>
          </div>
          <Link to={`/${auth}`}>
            <input 
            type="submit" 
            value="Login" 
            onClick={handleLogin}/>
          </Link>         
        </form>
      </div>
    </div>
  )
}

export default Login
